<template>
    <div class="monitorlogin_page">
        <div class="main">
            <div class="banner-box">
                <img src="/static/img/monitoringLogin/loginlog.png" alt="">
            </div>
            <div class="login-box">
                <div class="login-title">
                    <label>登陆系统管理</label>
                </div>
                <div class="login-body">
                <el-form ref="loginForm" :model="form" :rules="rules" label-width="80px">
                    <el-form-item label-width ="0" prop="username">
                        <el-input
                        placeholder="请输入账号"
                        v-model="form.username"
                        clearable
                        >
                        </el-input>
                    </el-form-item>
                    <el-form-item label-width ="0" prop="password"> 
                        <el-input
                        placeholder="请输入密码"
                        v-model="form.password"
                        type="password"
                        clearable
                        @keyup.enter.native="login" 
                        >
                        </el-input>
                    </el-form-item>
                    </el-form>
                </div>
                <div class="login-footer">
                    <div style="width:100%;">
                    <el-button type="primary" class="enter" @click="login" style="width:100%;height:45px;background:#2856A1;margin-bottom:10px;">登录</el-button>
                    </div>
                    <!-- <div style="width:100%;display:flex;justify-content:space-between;padding-top:8px;">
                    <el-checkbox v-model="checkedRemember" class="repa">记住账号</el-checkbox>
                    <el-button type="text" style="padding:0;" @click="forgetPassword" :disabled="loadForget">忘记密码</el-button>
                    </div> -->
                </div>
            </div>
        </div>
    </div>    
</template>

<script>
import APICONFIG from '../../../config/api.js'

export default {
  name: 'monitoringLogin',
  data() {
    return{
      netApi: null,
      language: '语言',
      checkedRemember: false, //记住密码
      loadForget: false,
      form: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 3, max: 40, message: '长度在 3 到 40 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 1, max: 40, message: '长度在 1 到 40 个字符', trigger: 'blur' }
        ]
      },
    }
  },
  created() {
    // if (localStorage.getItem('userActive')) {
    //   this.$router.push('/');
    // }
    this.netApi = APICONFIG().get('login');
    // 获取本地存储的用户名和密码
    let local = {
      password: localStorage.getItem('password'),
      username: localStorage.getItem('username'),
    }

    if (local.username) {
      this.form = local;
      this.checkedRemember = true
    }
  },
  methods: {
    login() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          this.sendLogin();
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    sendLogin() {
      this.$http.post(this.netApi.login, this.form).then(
        (res) => {
          if (res.body.status == 200) {
            console.log(res.body.data)
            if (this.checkedRemember) {
              //记住密码
              localStorage.setItem('username', this.form.username);
              localStorage.setItem('password', this.form.password);
            } else {
              //删除本地存储的用户名和密码
              localStorage.removeItem('username');
              localStorage.removeItem('password');
            }
            //登陆状态
            localStorage.setItem('userActive', this.form.username);
            //存储token
            localStorage.setItem('token', res.body.data.token);
            //登陆管理员等级
            localStorage.setItem('role', res.body.data.roleName[0]);
            // console.log(this.$router.options)
            // if (!this.$router.options.routes[1].children) {
            //   this.loadRouter();
            // }
            this.$router.push('/monitoringSystem/monitoring_system/monitoringRecord')
          } else {
            this.$message({
              message: res.body.message,
              type: 'warning'
            });
          }
        },
        (errer) => {
          this.$message({
            message: '服务错误' + errer,
            type: 'warning'
          });
        })
    },
    loadRouter() {

    },
    // 点击忘记密码
    forgetPassword() {
      if (!this.form.username) {
        this.$message({
          message: '先输入账号才能发送给指定邮箱！',
          type: 'warning'
        });
        return
      }
      this.$http.post(this.netApi.findPass, { username: this.form.username }).then(
        res => {
          if (res.body.status == 200) {
            this.$notify({
              title: '邮件发送提醒',
              message: '已给【' + this.form.username + '】用户的邮箱发送邮件，请注意查收！',
              type: 'success'
            });
            this.loadForget = true;
            setTimeout(() => { this.loadForget = false }, 10000);
          } else {
            this.$message({
              message: res.body.message,
              type: 'warning'
            });
          }
        },
        err => {
          this.$message.warning(err)
        })
    },
  }
}
</script>

<style lang="scss" scoped>
.monitorlogin_page{
  width: 100%;
  height: 100%;
  min-width: 1366px;
  min-height: 768px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 300px;
//   padding: 7% 15%;
  .main{
     width: 100%;
     height: 100%;
     min-width: 1140px;
    // width: 1200px;
    // height: 700px;
     background: tan;
     border-radius: 15px;
     display: flex;
     background: white;
     .banner-box{
         width: 53%;
         height: 100%;
         display: flex;
         justify-content: center;
         align-items: center;
         background: #234882;
         border-top-left-radius: 15px;
         border-bottom-left-radius: 15px;
     }
     .login-box{
         width: 47%;
         height: 100%;
         box-sizing: border-box;
         padding: 100px 140px;
         display: flex;
         flex-direction: column;
         align-items: center;
         .login-title{
             height: 100px;
             display: flex;
             justify-content: center;
             align-items: center;
             font-size: 32px;
             font-family: Microsoft YaHei;
             color: #234882;
             box-sizing: border-box;
             margin-top: 40px;
             margin-bottom: 50px;
         }
         .login-body{
             width: 95%;
             margin-top: 25px; 
         }
         .login-footer{
             width: 95%;
             margin-top: 25px; 
         }
     }
  }
}
</style>
<style lang="scss">
    .monitorlogin_page{
        .login-box{
            .login-body{
                .el-input__inner{
                    border: none;
                    border-bottom: 1px solid #dcdfe6;
                    border-radius: 0;
                    padding-left: 0;
                }
            }
        }
    }
</style>


